import { mapActions } from 'vuex';
import { getNativeAsset, getAssetColorStyle } from '@liquality/wallet-core/dist/src/utils/asset';
import cryptoassets from '@liquality/wallet-core/dist/src/utils/cryptoassets';
import { unitToCurrency } from '@liquality/cryptoassets';
import { shortenAddress } from '@liquality/wallet-core/dist/src/utils/address';
import SpinnerIcon from '@/assets/icons/spinner.svg';
import ChevronDown from '@/assets/icons/chevron_down.svg';
import ChevronRight from '@/assets/icons/chevron_right.svg';
const assets = {
  luna: 'Luna',
  uluna: 'Luna',
  uusd: 'UST',
  terra1ajt556dpzvjwl0kl5tzku3fc3p3knkg9mkv8jl: 'aUST',
  terra1u0t35drzyy0mujj8rkdyzhe264uls4ug3wdp3x: 'bLUNA',
  terra1747mad58h0w4y589y3sk84r5efqdev9q4r02pc: 'ANC'
};
const methods = {
  send: 'Send',
  transfer: 'Send',
  deposit_stable: 'Deposit',
  borrow_stable: 'Borrow Stable',
  repay_stable: 'Repay Stable',
  lock_collateral: 'Lock Collateral',
  withdraw_collateral: 'Withdraw Collateral',
  bond: 'Send',
  burn: 'Send',
  withdraw_unbonded: 'Withdraw',
  swap: 'Swap',
  withdraw_voting_tokens: 'Unstake',
  claim_rewards: 'Claim'
};
const message = {
  'wasm/MsgExecuteContract': 'Execute Contract',
  'bank/MsgSend': 'Transfer'
};
export default {
  components: {
    SpinnerIcon,
    ChevronDown,
    ChevronRight
  },
  data() {
    return {
      showData: false,
      selectedFee: 'average',
      error: null,
      loading: false,
      replied: false,
      customFee: null,
      gas: 0
    };
  },
  methods: {
    ...mapActions('app', ['replyPermission']),
    getAssetColorStyle,
    toggleshowData() {
      this.showData = !this.showData;
    },
    getMessageType(messageType) {
      return message[messageType];
    },
    async reply(allowed) {
      const optionsWithFee = {
        ...this.request.args[0],
        value: this.value,
        fee: this.request.args[0].fee,
        feeLabel: 'average'
      };
      const requestWithFee = {
        ...this.request,
        args: [optionsWithFee]
      };
      this.loading = true;
      this.error = null;
      try {
        const response = await this.replyPermission({
          request: requestWithFee,
          allowed
        });
        this.replied = true;
        if (response.error) {
          this.error = this.$tle(response.error);
        } else {
          window.close();
        }
      } finally {
        this.loading = false;
      }
    }
  },
  computed: {
    method() {
      return methods[this.request.args[0].method];
    },
    asset() {
      return assets[this.request.args[0].asset];
    },
    assetChain() {
      return getNativeAsset(this.asset);
    },
    address() {
      return this.request.args[0].to;
    },
    shortAddress() {
      return this.address ? shortenAddress(this.address) : 'New Contract';
    },
    data() {
      return this.request.args[0].data.msgs.map(msg => JSON.parse(msg));
    },
    request() {
      return {
        ...this.$route.query,
        args: JSON.parse(this.$route.query.args)
      };
    },
    feeInUsdValue() {
      return this.request.args[0].fee.slice(0, 4);
    },
    value() {
      return this.request.args[0].value;
    },
    amount() {
      if (!this.value) return 0;
      return unitToCurrency(cryptoassets[this.asset], this.value).toNumber();
    }
  },
  beforeDestroy() {
    // TODO: need to reply correctly when window is closed
    if (this.replied) return;
    this.reply(false);
  }
};